import axios from 'axios';
import React ,{useEffect, useState} from 'react';

const App=()=> {
  const [num, setNum] = useState();
  const [name, setName] = useState();
  const [moves, setMoves] = useState();

  useEffect(()=>{
    async function getData(){
      const res = await axios.get(`https://pokeapi.co/api/v2/pokemon/${num}`)
      setName(res.data.name);
      setMoves(res.data.moves.length);
    }
    getData();
  })

  const pokemon={
    background: "paleGreen",
    height:"100vh",
    color:"white" ,
    textShadow:"4px 4px 8px black",
    margin:"0px 10px",
    padding:"50vh 10px",
    fontwight:"bold"
  }

  return (
    <>
      <div className="main_div">
        <div className="banner" style={pokemon}><h1 >POKEMON CARD</h1></div>
        <div className="App">
          <h1>You choose <span style={{color:'paleGreen'}}>{num}</span>  value</h1>
          <h2  style={{color:"white" , background:"gainsboro" , width:"100%" ,padding:"5px"}}>Pokemon <span style={{color:'red' , fontSize:"50px"}}>{name}</span></h2>
          <h2>The number of moves is <span style={{color:'paleGreen'}}>{moves}</span> </h2>
          <hr style={{width:'100%' , background:'pink' , height:"8px" , border:"none"}}></hr>
          <select value={num} onChange={(event)=>{setNum(event.target.value)}}>
            <option value='1'>1</option>
            <option value='2'>2</option>
            <option value='3'>3</option>
            <option value='4'>4</option>
            <option value='25'>25</option>
          </select>
        </div>
      </div>
    </>
  );
}

export default App;